.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.button {
  background-color: #0F2747;
  border-radius: 10px;
  font-weight: 700;
  font-size: 18px;
  padding: 10px;
  border: none;
  cursor: pointer;
  color: #FFF; 
}

.button:hover {
    color: #95FFD4;
}

form {
  display: block;
  margin: 0 auto;
  text-align: center;
  max-width: 80%;
  
}

input {
    background-color: #FFF;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.15);
    width: 45%;
    border: none;
    font-size: 18px;
    padding: 22px;
    box-sizing: border-box;
    margin: 12px;
    display: block;
    align-items: center;
}

body {
  background: linear-gradient(180deg, rgb(137, 207, 241) 15%, rgba(58, 169, 225, 0) 100%);
  background-repeat: no-repeat;
}
